@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@100;400&display=swap);
:root {
  --heading-color: #444;
  --text-color: #999;
  --primary-color: #b7dcf2;
  --highlight-color: #d13267;
  --bg-color: #f4f4f4;
}

body {
  font-family: 'Roboto Slab', serif;
  margin: 0;
  font-size: 1em;
  background: #f4f4f4;
  background: var(--bg-color);
}
ul, li, p, h1, h2, h3, h4 {
  margin: 0;
  padding: 0;
}
ul {
  list-style-type: none;
}

.page-title {
  font-size: 1em;
  color: #444;
  color: var(--heading-color);
  display: inline-block;
}
.btn {
  background: #fff;
  padding: 8px 12px;
  border-radius: 4px;
  color: #b7dcf2;
  color: var(--primary-color);
  cursor: pointer;
  font-size: 0.9em;
  border: 1px solid #b7dcf2;
  border: 1px solid var(--primary-color);
}
.btn:hover {
  color: #fff;
  background-color: #b7dcf2;
  background-color: var(--primary-color);
}

label {
  display: block;
  margin: 24px auto;
}
label span {
  display: block;
  margin-bottom: 6px;
}
input, textarea {
  padding: 8px 6px;
  font-size: 0.9em;
  color: #777;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid #ddd;
  border-radius: 4px;
}
textarea {
  min-height: 160px;
}
.error {
  color: red;
  background: pink;
  border: 1px solid red;
  border-radius: 4px;
  padding: 8px;
  margin: 10px 0;
}
.App {
  display: flex;
}
.App .container{
  flex-grow: 1;
  padding: 0 60px;
}
.project-filter {
    margin: 30px auto;
}
.project-filter nav {
    display: flex;
    padding: 10px;
    background-color: #fff;
    border-radius: 4px;
}
.project-filter p {
    font-size: 0.9em;
    margin-right: 10px;
}
.project-filter button {
    background: transparent;
    border: 0;
    font-family: inherit;
    font-weight: bold;
    color: var(--text-color);
    cursor: pointer;
    border-right: 1px solid #e4e4e4;
    font-size: 0.9em;
}
.project-filter button:last-child {
    border: 0;
}
.project-filter button.active {
    color: var(--primary-color);
} 

  
.project-list {
    margin-top: 40px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    grid-gap: 20px;
}
.project-list a {
    background-color: #fff;
    padding: 16px;
    border-radius: 6px;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.05);
    text-decoration: none;
    color: inherit;
}
.project-list h4 {
    font-size: 0.9em;
    color: var(--heading-color);
}
.project-list p {
    color: var(--text-color);
    font-size: 0.9em;
}
.project-list .assigned-to {
    margin-top: 20px;
    padding-top: 10px;
    border-top: 1px solid #eee;
}
.project-list ul {
    margin: 10px 0;
    display: flex;
}
.project-list li {
    margin-right: 10px;
}
.project-list .avatar {
    width: 30px;
    height: 30px;
}
.avatar {
    display: inline-block;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    overflow: hidden;
  }
  .avatar img {
    width: 100%;
    height: 100%;
  }
.create-form {
    max-width: 600px;
}
.project-details {
    display: grid;
    grid-template-columns: 3fr 2fr;
    align-items: start;
    grid-gap: 60px;
}
.project-summary {
    background-color: #fff;
    padding: 30px;
    border-radius: 4px;
}
.project-summary .due-date {
    margin: 10px 0;
    font-size: 0.9em;
    color: var(--title-color);
}
.project-summary .details {
    margin: 30px 0;
    color: var(--text-color);
    line-height: 1.8em;
    font-size: 0.9em;
}
.project-summary h4 {
    color: var(--text-color);
    font-size: 0.9em;
}
.project-summary .assigned-users {
    display: flex;
    margin-top: 20px;
}
.project-summary .assigned-users .avatar {
    margin-right: 10px;
}
.project-summary + .btn {
    margin-top: 20px;
}
  
.project-comments label {
    margin-bottom: 0px;
}
.project-comments textarea {
    min-height: 40px;
    font-size: 1.5em;
}
  
.project-comments h4 {
    color: var(--heading-color);
}
.project-comments li {
    padding: 16px;
    border-radius: 4px;
    border: 1px solid #f2f2f2;
    margin-top: 20px;
    box-shadow: 3px 3px 5px rgba(0,0,0,0.05);
    background: #fff;
}
.comment-author {
    display: flex;
    align-items: center;
    color: var(--title-color);
}
.comment-author .avatar {
    width: 30px;
    height: 30px;
    margin-right: 10px;
}
.comment-date {
    color: var(--text-color);
    font-size: 0.9em;
    margin: 4px 0 10px;
}
.comment-content {
    color: var(--text-color);
    font-size: 0.9em;
}
  
.project-comments {
    max-height: 800px;
    overflow: auto;
    padding-right: 20px;
    scrollbar-width: 20px;
    scrollbar-color: #fff #e4e4e4;
}

.project-comments::-webkit-scrollbar {
    width: 6px;
}
.project-comments::-webkit-scrollbar-track {
    background: #fff;
}
.project-comments::-webkit-scrollbar-thumb {
    border-radius: 10px;
    border: 3px solid #e4e4e4;
}
